<template>
    <div class="mine-order">
        <!-- 路由链接 -->
        <ul class="mine-order-menu flex-row-col-cen">
            <li><router-link to="/mine-order/await-payment">待付款</router-link></li>
            <li><router-link to="/mine-order/await-send-goods">待发货</router-link></li>
            <li><router-link to="/mine-order/await-receiving-goods">待收货</router-link></li>
            <li><router-link to="/mine-order/completed">已完成</router-link></li>
            <li><router-link to="/mine-order/after-sale">售后</router-link></li>
        </ul>
        <!-- 路由出口 -->
        <router-view  class="mine-order-content"></router-view>
    </div>
</template>
<script>
export default {
    name:"MineOrder",
    data() {
        return {}
    }
}
</script>

<style lang="less" scoped>
.mine-order {
    width: 100%;
    height: 100%;
    .mine-order-menu {
        width: 100%;
        height: 64px;
        line-height: 64px;
        background: #fff;
        border-bottom: 1px solid #ccc;
        padding: 0 15px;
        position: fixed;
        top: 64px;
        left: 0;
        z-index: 99;
        li {
            width: 20%;
            a {
                width: 100%;
                height: 100%;
                display: inline-block;
                color: #333;
                  
            }
            a.active{
                color: #f00;
                position: relative;
                &::after {
                    display: inline-block;
                    width: 100%;
                    height: 1px;
                    content: "";
                    background: #f00;
                    position: absolute;
                    left: 0;
                    bottom: 0;
                }
            }
            
        }
    }
    .mine-order-content {
        width: 100%;
        height: calc(100% - 65px);
        padding-top: 65px;
    }
}
</style>




